<template>
  <div class="dw-answer-custom-theme">
    <div class="custom-element" style="display: none;">
      <p> 使用CSS变量的样式 This text has a custom color!</p>
      <div>
        <div class="original-color"></div>
        <div class="near-color1"></div>
      </div>
      <el-button type="primary" @click="red('red')">按钮</el-button>
    </div>
    <el-row>
      <el-col v-if="(answerProps.sid!==undefined && answerProps.sid!==null)">
        <dw-answer-survey-main :answer-props="answerProps" :ext-props="extProps"></dw-answer-survey-main>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import DwAnswerSurveyMain from '../../../components/dw-survey-comp/dw-answer-comp/DwAnswerSurveyMain.vue'

export default {
  name: 'DwAnswerSurvey',
  components: {DwAnswerSurveyMain},
  props: {
    extProps: {type: Object, default: () => {}},
    answerProps: {type: Object, default: () => { return {sid: null, answerId: null, anPwd: null} }}
  },
  data () {
    return {
    }
  },
  mounted () {
    if (this.answerProps===null || !this.answerProps.hasOwnProperty('sid') || this.answerProps.sid===null) {
      // const sid = this.$route.params.id
      // const answerId = this.$route.params.answerId
    }
    // 设置CSS变量的值
  },
  methods: {
    red (color) {
      // 设置CSS变量的值
      // document.documentElement.style.setProperty('--custom-color', color)
      // const originalColor = 'rgb(255, 0, 0)' // 红色
      document.documentElement.style.setProperty('--dw-answer-primary-color', color)
    }
  }
}
</script>
<style scoped>

</style>
<style>
@import '../../../assets/css/dw-answer.css';
</style>
<style>
/* 定义CSS变量 */
:root {
  --custom-color: blue; /* 默认值 */
  --original-color: #ff8800;
  --near-color1: #ff8800;
}

.custom-element p {
  color: var(--custom-color); /* 使用CSS变量 */
}
/* 给定颜色 */
.original-color {
  background-color: var(--original-color); /* 给定颜色 */
  width: 100%;
  height: 100px;
}

/* 计算的相近颜色 */
.near-color1 {
  background-color: var(--near-color1); /* 亮色 */
  width: 100%;
  height: 100px;
}
</style>



